import React from 'react';
import { List, SimpleCell, Spinner, Tabs, TabsItem, Avatar, PullToRefresh } from '@vkontakte/vkui';
import axios from 'axios';
import { VKMiniAppAPI } from '@vkontakte/vk-mini-apps-api';

const api = new VKMiniAppAPI();
const url = "https://api-waste.hhos.ru/api/";

class Rating extends React.Component {
    constructor(props) {
       super(props); 
        this.state = {
          list_main:null,
          activeTab:'all',
          list_friends:null,
          fetching:false
        }
    }

    componentDidMount() {
      
      axios.post(url, JSON.stringify({type:'get_rating_all'}))
      .then(response => {
          console.log(response);   
        this.setState({list_main:response.data})
      })
    
        axios.post(url, JSON.stringify({type:'get_rating_frieds', url:window.location.href}))
        .then(res => {
            this.setState({list_friends:res.data});
        }) 

    }

    onRefresh = () => {
        this.setState({ fetching: true });
        axios.post(url, JSON.stringify({type:'get_rating_all'}))
        .then(response => {
            console.log(response);   
          this.setState({list_main:response.data, fetching:false})
        })
    }

    render() {
       return(
          <div> 
              <Tabs>
                  <TabsItem selected={this.state.activeTab == 'all'} onClick={()=> this.setState({activeTab: 'all'})}>Общий</TabsItem>
                  <TabsItem selected={this.state.activeTab == 'friends'} onClick={()=> this.setState({activeTab: 'friends'})}>Среди друзей</TabsItem>
              </Tabs>
           {this.state.activeTab == 'all' ?    
           <PullToRefresh isFetching={this.state.fetching} onRefresh={this.onRefresh}>   
            {this.state.list_main == null ? <Spinner size='large' style={{color:"var(--accent)"}} /> :
           <List>  
               {this.state.list_main.map((data, i) =>
                   <SimpleCell description={data.mass+" кг"} disabled before={<Avatar style={this.props.id == data.vk_id ? {border: "1px solid var(--accent)"}:null}>{i+1}</Avatar>} key={i} href={'https://vk.com/id'+data.vk_id} indicator={<Avatar src={data.img} />} style={{textDecoration: 'none'}}>
                    {data.name}
                   </SimpleCell>
               )}
           </List>
           } 
          </PullToRefresh>:
                     <div>   
                     {this.state.list_main == null ? <Spinner size='large' style={{color:"var(--accent)"}} /> :
                    <List>  
                        {this.state.list_main.data.map(
                            <SimpleCell>
         
                            </SimpleCell>
                        )}
                    </List>
                    } 
                   </div>
           }
          </div> 
       )
    }
}


export default Rating;